/*
      
    SEND_MESSAGE  -    
*/

import { useContext, useState, useRef } from "react";
import { SocketContext, SOCKET_EVENT } from "../service/socket";

function MessageForm({nickname}){
    const [typingMessage, setTypingMessage] = useState("");
    const socket = useContext(SocketContext);
    const messageInput = useRef();

    const handleChangeTypingMessage = (e) => {
        setTypingMessage(e.target.value);
    };

    const onCheckEnter = (e) => {
        if(e.key === "Enter"){
            e.preventDefault();
            // , shiftKey   (Enter)
            if(!e.shiftKey){ // textarea shiftKey + Enter ,  Enter Submit
                //  
                const noContent = typingMessage.trim() === "";
                    
                //      X
                if(noContent){
                    return;
                }
            
                //   nickname message SEND_MESSAGE    
                socket.emit(SOCKET_EVENT.SEND_MESSAGE, {
                    nickname,
                    content : typingMessage,
                });
                // state   
                setTypingMessage("");
            }
            messageInput.current.focus();
        }
    };

    //   
    const handleSendMessage = (e) => {
        e.preventDefault();
        //  
        const noContent = typingMessage.trim() === "";

        //      X
        if(noContent){
            return;
        }

        //   nickname message SEND_MESSAGE    
        socket.emit(SOCKET_EVENT.SEND_MESSAGE, {
            nickname,
            content : typingMessage,
        });
        // state   
        setTypingMessage("");
    }

    return(
        <form className="card message-form" onKeyPress={onCheckEnter}>
            <div className="d-flex align-items-center">
                <textarea
                    className="form-control"
                    maxLength={400}
                    autoFocus
                    value={typingMessage}
                    onChange={handleChangeTypingMessage}
                    ref={messageInput}/>
                <button
                    type="button"
                    className="btn btn-primary send-btn"
                    onClick={handleSendMessage}></button>
            </div>
        </form>
    );
}

export default MessageForm;