import { useState } from "react";

/*
    1. input   NicknameForm    state 
    2. Submit   input  App   App   nickname state 
*/

function NicknameForm({ handleSubmitNickname }){
    const [nickname, setNickname] = useState('');

    const onCheckEnter = (e) => {
        if(e.key === "Enter"){
            handleSubmit(e);
        }
    };

    const handleChangeNickname = (e) => {
        setNickname(e.target.value);
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        handleSubmitNickname(nickname);
        setNickname('');
    };

    return(
        <form className="d-flex nickname-form" onKeyPress={onCheckEnter}>
            <div className="card d-flex flex-row align-items-center">
                <label htmlFor="user-name-input" style={{width: 60}}>
                    
                </label>
                <input
                    type="text"
                    className="form-control w300"
                    id="user-name-input"
                    maxLength={12}
                    value={nickname}
                    onChange={handleChangeNickname}/>
                <button
                    type="button"
                    className="btn btn-primary send-btn"
                    value=""
                    onClick={handleSubmit}></button>
            </div>
        </form>
    );
}

export default NicknameForm;