import { useEffect, useState, useRef, useCallback } from 'react';
import ChatRoom from './ChatRoom';
import NicknameForm from './NicknameForm';
import { socket, SocketContext, SOCKET_EVENT } from '../service/socket';
import { useLocation, useNavigate } from 'react-router-dom';
import UserListForm from './UserListForm';

/*
    Event List
    "JOIN_ROOM" :    
    "UPDATE_NICKNAME" :    
    "SEND_MESSAGE" :    
    "RECEIVE_MESSAGE" :    

    Socket.io Tip!
    emit -  <->   !!  !!
    on -  <->   !!  !!
*/

// ,    (confirm)
const useConfirm = (message = "", onConfirm, onCancel) => {
    if (!onConfirm || typeof onConfirm !== "function") { 
        return; //  onConfirm  onConfirm   return 
    }
    if (!onCancel || typeof onCancel !== "function") { // onCancle  
        return;
    }

    const confirmAction = () =>{
        if(window.confirm(message)){
            onConfirm();
        }else{
            onCancel();
        }
    }
    return confirmAction;
};

function Home() {
    const { state } = useLocation();
    const navigate = useNavigate();
    const prevNickname = useRef(null); // prevNickname   rerender .
    const [nickname, setNickname] = useState(state.nickname);

    const handleSubmitNickname = useCallback(newNickname => {
        prevNickname.current = nickname; //   prevNickname 
        setNickname(newNickname); //   nickname state 
    }, [nickname]);


    const handleServerExitSuccess = () => {
        socket.emit("ROOM_EXIT", {nickname}); //      
        navigate('/login');
    }
    const handleServerExitCancle = () => {
        console.log(".");
    }
    const handleServerExit = useConfirm(
        " ?",
        handleServerExitSuccess,
        handleServerExitCancle
    )

    useEffect(() => {
    //   
        if(prevNickname.current){
          socket.emit(SOCKET_EVENT.UPDATE_NICKNAME, { //     
            prevNickname: prevNickname.current, //  
            nickname, //  
            });
        } else {
          // JOIN      (   App    JOIN_ROOM emit)
            socket.emit(SOCKET_EVENT.JOIN_ROOM, {nickname});
        }
    }, [nickname]);
    /*
    App Component SocketContext.provider .
    */
    return (
    <SocketContext.Provider value={socket}>
        <div className='d-flex justify-content-center'>
            <div className='d-flex flex-column justify-content-center align-items-center vh-100'>
                <div>
                    <h4 className='home-title'>  room 1 .</h4>
                </div>
                <NicknameForm handleSubmitNickname={handleSubmitNickname}/>
                <ChatRoom nickname={nickname}/>
            </div>
            <div className='d-flex flex-column justify-content-center align-items-center vh-100'>
                <UserListForm/>
                <button
                    type="button"
                    style={{marginTop:20}}
                    className="btn btn-primary send-btn exit-btn"
                    onClick={handleServerExit}
                    ></button>
            </div>
        </div>
    </SocketContext.Provider>
    );
}

export default Home;
