/*
        - RECEIVE_MESSAGE     ,    
    ,   
*/

import { useState, useCallback, useContext, useEffect, useRef } from "react";
import { SocketContext, SOCKET_EVENT, makeMessage } from "../service/socket";
import MessageForm from "./MessageForm";

function ChatRoom({nickname}){
    // message = { nickname, content, time }
    const [messages, setMessages] = useState([]);
    const chatWindow = useRef(null);
    const socket = useContext(SocketContext); // useContext  socket  

    //      - uesRef        
    const moveScrollToReceiveMessage = useCallback(()=> {
        if(chatWindow.current){
            chatWindow.current.scrollTo({
                top: chatWindow.current.scrollHeight,
                behavior: "smooth",
            });
        }
    }, []);

    //  RECEIVE_MESSAGE   - messages state  
    const handleReceiveMessage = useCallback(pongData => {
        const newMessage = makeMessage(pongData); //      
        setMessages(messages => [...messages, newMessage]);
        moveScrollToReceiveMessage();
    }, [moveScrollToReceiveMessage]);

    // ChatRoom    RECEIVE_MESSAGE  ,  RECEIVE_MESSAGE  
    useEffect(()=>{
        socket.on(SOCKET_EVENT.RECEIVE_MESSAGE, handleReceiveMessage); //   
        return () => {
            socket.off(SOCKET_EVENT.RECEIVE_MESSAGE, handleReceiveMessage); //   
        }
    }, [socket, handleReceiveMessage]);

    return (
        <div className="d-flex flex-column chat-room">
            <div className="text-box">
                <span>{nickname}</span> !
            </div>
            <div className="chat-window card" ref={chatWindow}>
                {
                    messages.map((message, index) => {
                        const { content, time } = message;
                        const chatNickname = message.nickname;
                        // message  map    item  justify-content-end
                        return (
                            <div key={index}>
                                {   // nickname chatNickname          
                                    // nickname chatNickname         
                                    nickname === chatNickname ? ( 
                                        <div key={index} className="d-flex justify-content-end">
                                            <div className="time" style={{marginRight: 10}}> {time} </div>
                                            {
                                                chatNickname && <div className="message-nickname">{chatNickname} : </div>
                                            }
                                            <div> {content} </div>
                                        </div>
                                    ) : (
                                        <div key={index} className="d-flex flex-row">
                                            {
                                                chatNickname && <div className="message-nickname">{chatNickname} : </div>
                                            }
                                            <div>{content}</div>
                                            <div className="time">{time}</div>
                                        </div>
                                    )
                                }
                            </div>
                        );
                    })
                }
            </div>
            <MessageForm nickname={nickname}/>
        </div>
    );
}

export default ChatRoom;
